import React from 'react';
import Pp from '../../../images/intstractor-profile/pp.png';
import more from '../../../images/instractor-home/more.svg';
import clock from '../../../images/icons/clock.svg';



export default function SessionsCard(props){
    return(
    <section className="col-md-3 mb-3 col-6">
        <figure className="mb-1">
            <img src={Pp}  alt="instractor" className="rounded img-fluid"/>
        </figure>
        <article className="row m-0 align-items-center">
            <div className="col-10 pl-0">
                <p className="mb-0">Dianna Kozey</p>
                <p className="mb-0 text-green d-inline-block pr-3">Tahfeez </p>
                <p className="text-green mb-0 d-inline-block">
                    <img src={clock} alt="clock"/> 8:36 PM</p>
            </div>
            <div className="col-2 text-right p-0"><img src={more} alt=""/></div>
        </article>
    </section>
    )
}