import React from 'react';
import Pp from '../../../images/intstractor-profile/pp.png';
import more from '../../../images/instractor-home/more.svg';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import link from '../../../images/icons/link.svg'



export default function SessionsCardPast(props){
    return(
    <section className="col-md-3 col-6 mb-3">
        <figure className="mb-1">
            <img src={Pp} style={{maxHeight : '250px'}} alt="instractor" className="rounded img-fluid"/>
        </figure>
        <article className="row m-0 align-items-center">
            <div className="col-10 pl-0">
                <p className="mb-0">Dianna Kozey</p>
                <p className="mb-0 text-green d-inline-block pr-3">Tahfeez </p>
                <p className="text-green mb-0 d-inline-block">
                    <img src={link} alt="clock"/></p>
            </div>
            <div className="col-2 text-right p-0"><DeleteOutlineIcon className="text-danger"/></div>
        </article>
    </section>
    )
}